@-webkit-keyframes bubble {
  0%    {opacity: 0%; top: 26.56%}
  15%    {opacity: 0%; top: 26.56%}
  75%  {opacity: 100%; top:23.56%;} 
}

@keyframes bubble {
  0%    {opacity: 0%; top: 26.56%}
  15%    {opacity: 0%; top: 26.56%}
  75%  {opacity: 100%; top:23.56%;} 
}

@-webkit-keyframes bubble-1s {
  0%   {opacity: 0%; top: 37%;}
  25%   {opacity: 0%; top: 37%;}
  55%  {opacity: 100%; top: 34%;}
}

@keyframes bubble-1s {
  0%   {opacity: 0%; top: 37%;}
  25%   {opacity: 0%; top: 37%;}
  55%  {opacity: 100%; top: 34%;}
}

@-webkit-keyframes bubble-3s {
  0% {opacity: 0%; top:69%;}
  20%   {opacity: 0%; top: 69%;}
  35%   {opacity: 100%; top: 66%;}
}

@keyframes bubble-3s {
  0% {opacity: 0%; top:69%;}
  20%   {opacity: 0%; top: 69%;}
  35%   {opacity: 100%; top: 66%;}
}

.bubble-animation{
  -webkit-animation: bubble 1s ease-in-out;
          animation: bubble 1s ease-in-out;
}

.bubble-animation-1s{
  -webkit-animation: bubble-1s 1.6s ease-in-out ;
          animation: bubble-1s 1.6s ease-in-out ;
}

.bubble-animation-3s{
  -webkit-animation: bubble-3s 2.8s ease-in-out ;
          animation: bubble-3s 2.8s ease-in-out ;
}

@-webkit-keyframes mouseover {
  0%    {width: 98%;}
  75%   {width: 100%;}  
  100%  {width: 99.9%;}  
}

@keyframes mouseover {
  0%    {width: 98%;}
  75%   {width: 100%;}  
  100%  {width: 99.9%;}  
}

.hover-animation{
  -webkit-animation: mouseover .5s ease-in-out forwards;
          animation: mouseover .5s ease-in-out forwards;
}

@-webkit-keyframes vibrate {
  0%    {-webkit-transform: translate(-50%, 18.5%) rotate(0);transform: translate(-50%, 18.5%) rotate(0);}
  10%    {-webkit-transform: translate(-50%, 18.5%) rotate(1deg);transform: translate(-50%, 18.5%) rotate(1deg);}
  30%    {-webkit-transform: translate(-50%, 18.5%) rotate(-1deg);transform: translate(-50%, 18.5%) rotate(-1deg);}
  50%    {-webkit-transform: translate(-50%, 18.5%) rotate(1deg);transform: translate(-50%, 18.5%) rotate(1deg);}
  65%    {-webkit-transform: translate(-50%, 18.5%) rotate(0deg);transform: translate(-50%, 18.5%) rotate(0deg);}
}

@keyframes vibrate {
  0%    {-webkit-transform: translate(-50%, 18.5%) rotate(0);transform: translate(-50%, 18.5%) rotate(0);}
  10%    {-webkit-transform: translate(-50%, 18.5%) rotate(1deg);transform: translate(-50%, 18.5%) rotate(1deg);}
  30%    {-webkit-transform: translate(-50%, 18.5%) rotate(-1deg);transform: translate(-50%, 18.5%) rotate(-1deg);}
  50%    {-webkit-transform: translate(-50%, 18.5%) rotate(1deg);transform: translate(-50%, 18.5%) rotate(1deg);}
  65%    {-webkit-transform: translate(-50%, 18.5%) rotate(0deg);transform: translate(-50%, 18.5%) rotate(0deg);}
}

.vibrate{
  -webkit-animation: vibrate .3s ease-in-out;
          animation: vibrate .3s ease-in-out;
}

  
  
