}




.hyper-links{
  font-family: Neue Hass Grotesk;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  color: #000;
  margin-bottom: 20px;
  
}


.copy-project{
  text-align: center;
  font-style: normal;
  font-weight: normal;
